import React, { Component } from 'react';

class Case extends Component {
	constructor(props){
		super(props);
		let nMaxHeight = window.innerHeight;

		this.state = {
			PosX : props.number === undefined ? props.posx : this.getPosXFromColumnt(props.number), 
			PosY : props.number === undefined ? props.posy : nMaxHeight * .8, 
			RelX : 0,
			RelY : 0,
			IsDragging : false,
			IsBigger: false,
			IsBaseline: false,
			width: '8%',
			height: '15%',
			text: props.number === undefined ? 'Fill me out' : 'I\'m queued up',
			TextAreaVisibility: 'hidden',
			TextDisplay: 'block'
		};
		this.onMouseDown = this.onMouseDown.bind(this);
		this.onMouseUp = this.onMouseUp.bind(this);
		this.onMouseMove = this.onMouseMove.bind(this);
		this.onDoubleClick = this.onDoubleClick.bind(this);
	}

	componentDidUpdate(prevProps, prevState, snapshot){
	    if (this.state.IsDragging && !prevState.IsDragging) {
			document.addEventListener('mousemove', this.onMouseMove)
			document.addEventListener('mouseup', this.onMouseUp)
		  } else if (!this.state.IsDragging && prevState.IsDragging) {
			document.removeEventListener('mousemove', this.onMouseMove)
			document.removeEventListener('mouseup', this.onMouseUp)
		  }
	  
	}
	// we could get away with not having this (and just having the listeners on
	// our div), but then the experience would be possibly be janky. If there's
	// anything w/ a higher z-index that gets in the way, then you're toast,
	// etc.

	// calculate relative position to the mouse and set dragging=true
	onMouseDown (e) {
		// only left mouse button
		if (e.button !== 0) 
			return;
		if (this.state.IsBigger === true)
			return;
		var pos = this.refs.test1.getBoundingClientRect();
		this.setState({
		IsDragging: true,
		RelX: e.pageX - pos.left,
		RelY: e.pageY - pos.top
		});
		e.preventDefault();
	}

	onMouseUp(e) {
		let nMaxWidth = window.innerWidth;
		let nColumnWidth = nMaxWidth / 10.0;

		let nFinal = 0;
		for (let i = 0; i < 10; i++) {
			let nMin = i * nColumnWidth;
			let nMax = (i+1) * nColumnWidth;

			if ((this.state.PosX > nMin) && (this.state.PosX <= nMax)){
				nFinal = nMin + nMaxWidth * 0.00795;
			}
		}

		this.setState({IsDragging: false, PosX: nFinal});
		e.stopPropagation();
		e.preventDefault();
	}

	getPosXFromColumnt(pnColumn){
		let nMaxWidth = window.innerWidth;
		let nColumnWidth = nMaxWidth / 10.0;

		return pnColumn * nColumnWidth + nMaxWidth * 0.008

	}

	onMouseMove(e) {
		if (!this.state.IsDragging) return;
		this.setState({
			PosX: e.pageX - this.state.RelX,
			PosY: e.pageY - this.state.RelY
		});
		e.stopPropagation();
		e.preventDefault();
	}

	onDoubleClick(e) {
		// only left mouse button
		if (e.button !== 0) 
			return;
		if (this.state.IsBigger === true) {
			this.setState({
				IsBigger: false,
				width: '8%',
				height: '15%',
				text: this.refs.taValue.value,
				TextAreaVisibility: 'hidden',
				TextDisplay: 'block',
				IsBaseline: this.refs.chkBaseline.checked
			});
		} else {
			this.refs.taValue.select();
			this.setState({
				IsBigger: true,
				width: '24%',
				height: '30%',
				TextAreaVisibility: 'visible',
				TextDisplay: 'none'
			});
		}

	}

	render() {
		return (
			<div ref='test1' onMouseDown = {this.onMouseDown} onDoubleClick={this.onDoubleClick} className='case-box'
				style={{
					zIndex:this.state.IsBigger === true ? '999' : '50', 
					width:this.state.width,
					height:this.state.height,
					left:this.state.PosX + 'px', 
					top:this.state.PosY + 'px',
					backgroundColor: this.props.color,
					borderColor: this.state.IsBaseline === true ? 'red' : 'black'
				}}>
				<p style={{display:this.state.TextDisplay}}>
					{this.state.text}
				</p>
				<div style={{
					visibility:this.state.TextAreaVisibility, height:'100%'
					}}>
					<textarea ref='taValue'  width='100%' defaultValue={this.state.text}>
					</textarea>
					<br/>
					<label>
						<input type="checkbox" defaultChecked={this.state.IsBaseline} ref="chkBaseline" ></input>
						Baseline
					</label>
				</div>
			</div>
		);
	}
}

export default Case;



