import React, { Component } from 'react';
import './App.css';
import {DropdownButton, MenuItem} from 'react-bootstrap';

import SetupFile from './setup.json';
import Case from './Case.js';

class App extends Component {
	constructor(props){
		super(props);

		let oCols = [];
		oCols = SetupFile.columns;

		let oTeams = [];
		oTeams = SetupFile.teams;

		let oCases = [[0,0]];
		let oBacklogCases = [[0,0], [0,0], [0,0], [0,0]];

		this.state = {
			cols: oCols,
			cases: oCases,
			color: oTeams[0][1],
			backlogCases: oBacklogCases,
			teams: oTeams
		};

		this.onClick = this.onClick.bind(this);
		this.onSelect = this.onSelect.bind(this);

	}

	onSelect(e) {
		
		this.setState({color: this.state.teams[e][1]});
	}

	onClick(e) {
		let oCases = this.state.cases;
		oCases.push([0,0]);

		//let uriContent = "data:application/octet-stream," + encodeURIComponent("test");
		//let newWindow = window.open(uriContent, 'testtest');

		this.setState({cases:oCases});
	}

	render() {
		return (
			<div style={{width:'100%', height:'100%'}}>
				<div className="board">
					<div className="row-header">
						{this.state.cols.map((oCol) => <div className="cell-header" key={oCol}>
							<h1>{oCol}</h1>
						</div>)}

					</div>
					<div className="row-cases">
					{this.state.cols.map((oCol) => <div className="col-cases"  key={oCol}>
					</div>)}

					</div>
					<input type='button' onClick={this.onClick} style={{margin:'3px'}} value='ADD MORE CASES'></input>
					<DropdownButton title='Team' id='drpTeams' onSelect={this.onSelect}>
					{this.state.teams.map((oTeam, nIndex) =>
						<MenuItem key={nIndex} eventKey={nIndex} value={oTeam[1]} >{oTeam[0]}</MenuItem>
					)}
					</DropdownButton>
					<div className="row-queue">

					</div>
				</div>
			{this.state.cases.map((oCase, nIndex) => <Case key={nIndex} posx = {oCase[0]} posy={oCase[1]} color={this.state.color} />)}
			{this.state.backlogCases.map((oCase, nIndex) => <Case key={nIndex} number={nIndex} color={this.state.color} />)}
			</div>
		);
	}
}

export default App;



